/* Первые видимые элементы страницы (Приветствие и картинка) */
main {
  width: 100%;
  background-color: black;
  color: white;
  padding-top: 70px;

 
}

/* Общий span */
main span {
  color: #c770f0;
}
/* -------------------- */

main .vision-section {
  display: flex;
  align-content: center;
  justify-content: space-around;
  padding: 10px 50px 100px;
  height: auto;
}

main .vision-section .text-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px 10px;
  width: 50%;
}

main .vision-section .text-box h1:first-child {
  padding-left: 10px;
}

main .vision-section .text-box h1 {
  padding: 10px 0;
  position: relative;
  bottom: 30px;
  font-size: 3em;
}

main .vision-section .text-box p {
  font-size: 1.3em;
}

main .vision-section .image {
  height: auto;
  max-height: 450px;
  padding: 0 10px;
}

/* Стрелка */
main .icon-down {
  width: 100%;
  font-size: 3em;
  display: flex;
  justify-content: center;
  animation: jump 2s infinite;
}

@keyframes jump {
  0%,
  100% {
    transform: translateY(0);
    opacity: 1; /* Полностью видимая */
  }
  50% {
    transform: translateY(-10px);
    opacity: 0.7; /* Немного прозрачная */
  }
}

/* -----End----- */

/* Навыки Skills */
main .intro {
  display: flex;
  justify-content: space-evenly;

  margin: 70px 50px;
  padding: 0 20px;
}

main .intro .text-box {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  max-width: 50%;
  margin: 0 50px;
}

main .intro .text-box p {
  padding: 20px 20px;
  margin: 25px 50px;
  font-size: 1.5em;
  text-indent: 20px;
  border: 1px solid white;
  border-radius: 3px;
  box-shadow: 10px 5px 5px blueviolet;
  text-align: center;

  background: linear-gradient(
    -45deg,
    #000000,
    rgba(138, 43, 226, 0.2),
    #000000,
    rgba(138, 43, 226, 0.2)
  );
  background-size: 400% 400%;
  animation: gradient 10s ease infinite;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

main .intro .text-box p:first-child,
main .intro .text-box p:last-child {
  position: relative;
  top: 0;
  right: 70px;
}

main .intro .text-box p:nth-child(2) {
  position: relative;
  top: 0;
  left: 70px;
}

main .intro .avatar img {
  height: auto;
  max-height: 350px;
  border-radius: 50%;
}

/* Последний элемент Home (end-home) */
main .end-home {
  display: flex;
  justify-content: center;
  padding: 50px 0;
  font-size: 1.3em;
}

main .contact-me {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 500px;
  margin: 20px auto;
  padding: 20px 10px;
  border: 1px solid white;
  border-radius: 5px;
  transition: box-shadow 1s ease;
}

main .contact-me:hover {
  box-shadow: 0px 5px 15px blueviolet;
}

main .contact-me h2 {
  padding-bottom: 10px;
}

/* /////////////////////////////////////////////// */
@media (max-width: 767px) {
  main .vision-section {
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 10px 20px 30px;
  }

  main .vision-section .image {
    width: 300px;
  }

  main .vision-section .text-box {
    padding: 10px 10px;
    margin-bottom: 30px;
    width: 100%;
    font-size: 1em;
  }

  main .intro {
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    align-items: center;
    margin: 70px 20px 40px;
    padding: 0 20px;
  }

  main .intro .text-box {
    max-width: 100%;
    margin: 0 10px 20px;
  }

  main .intro .text-box p {
    font-size: 1.3em;
    max-width: 100%;
    padding: 20px 10px;
    margin: 15px 10px;
  }

  main .intro .text-box p:nth-child(2) {
    left: 15px;
  }

  main .intro .text-box p:first-child,
  main .intro .text-box p:last-child {
    right: 15px;
  }

  main .intro .avatar img {
    max-height: 200px;
  }

  main .end-home {
    padding: 50px 20px;
    font-size: 1.2em;
    text-align: center;
  }

  main .contact-me {
    width: 90%;
    margin: 20px auto;
    padding: 10px 10px;
    text-align: center;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  main .vision-section .image {
    height: auto;
    max-height: 250px;
    padding: 0 10px;
  }

  main .vision-section .text-box {
    padding: 10px 10px;
    margin-bottom: 30px;
    width: 70%;
  }

  main .intro .text-box {
    max-width: 70%;
    margin: 0 10px;
  }

  main .intro .text-box p {
    font-size: 1.3em;
  }

  main .intro .text-box p:nth-child(2) {
    left: 80px;
  }

  main .intro .avatar img {
    max-height: 200px;
  }

  main .end-home {
    padding: 50px 20px;
    font-size: 1.2em;
    text-align: center;
  }
}

@media screen and (min-width: 1399px) {
  main .vision-section {
    padding: 10px 50px 10px;
    height: auto;
  }

  header nav .link .icons {
    padding: 0 10px;
  }
}
